import React  from 'react'
import Scroll from 'react-bscroll'
// import Component from '../../tools/shouldComponent'
import {connect} from 'react-redux'
import { DatePicker, List,Picker,InputItem ,Button,WingBlank,Modal} from 'antd-mobile';
import {getListAll,addRecord,ADD_RECORD_SUCCESS} from '../../store/actions'
const alert = Modal.alert;
const nowTimeStamp = Date.now();
const now = new Date(nowTimeStamp);

class Submit extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            visible:false,
            date: now,
            money:'',
            what:'',
            userArr:[]
        }
    }
    render(){
        let {date,money,what,userArr,visible}=this.state
        let {userList}=this.props;
        let seasons=this.getNewUserArr(userList)
        return <Scroll  click={true}>
            <div className="Submit">
                <WingBlank>
                    <List>
                        <DatePicker
                        mode="date"
                        title="Select Date"
                        extra="Optional"
                        value={date}
                        onChange={Time => {this.setState({ date:Time })}}
                        >
                        <List.Item arrow="horizontal"></List.Item>
                        </DatePicker>
                        <Picker
                        data={seasons}
                        title=""
                        cascade={false}
                        extra="()"
                        value={userArr}
                        onChange={v => this.setState({ userArr: v })}
                        onOk={v => this.setState({ userArr: v })}
                        >
                        <List.Item arrow="horizontal"></List.Item>
                        </Picker>
                        <InputItem placeholder="" value={money} onChange={(money)=>{;this.setState({money})}}></InputItem>
                        <InputItem placeholder="" value={what} onChange={(what)=>{;this.setState({what})}}></InputItem>
                    </List>
                    <div className="btns" style={{display:'flex',justifyContent:'center',padding:'.3rem 0'}}>
                        <Button type="primary" inline size="small" style={{ marginRight: '.2rem' }} onClick={()=>{this.setState({visible:true})}}></Button>
                        <Button type="warning" inline size="small" style={{ marginRight: '.2rem' }} onClick={this.clearBtn.bind(this)}></Button>
                    </div>
                    <Modal
                    visible={visible}
                    transparent
                    maskClosable={false}
                    onClose={()=>{this.setState({visible:false})}}
                    title=""
                    footer={[{ text: '', onPress: this.submitBtn.bind(this)},{ text: '', onPress: () => { console.log('00');this.setState({visible:false})} }]}
                    >
                    <div >
                        <p><span></span>:<span>{this.getDate(date)}</span></p>
                        <p><span></span>:<span>{userArr}</span></p>
                        <p><span></span>:<span>{money}</span></p>
                        <p><span></span>:<span>{what}</span></p>
                    </div>
                    </Modal>
                </WingBlank>
            </div>
        </Scroll>
    }
    componentDidMount(){
        this.props.changeRedux(getListAll)
    }
    componentWillReceiveProps(nextProps){
        let{addRecordSuccess,LoadingShow}=nextProps;
        if(LoadingShow){
            return false;
        }
        if(addRecordSuccess===null)return false;
        let title=addRecordSuccess?'':''
        this.addAlert(title,this.addRecord.bind(this))
    }
    getNewUserArr(userList){
        return [userList.map(item=>{
            return {
                label:item.user,
                value:item.user
            }
        })] 
    }
    getDate(datetime){
        var year=datetime.getFullYear();//(4,1970)
        var month=datetime.getMonth()+1;//(0-11,01,1)
        if(month<=9){
            month="0"+month;
        }
        var date=datetime.getDate();//(1-31)
        if(date<=9){
            date="0"+date;
        }
        return year+"-"+month+"-"+date
    }
    submitBtn(){
        let {date,money,what,userArr}=this.state;
        
       
        this.setState({visible:false});
        this.clearBtn();
        if(userArr.length===0||what===''||money===''){
            let title=""
            return this.addAlert(title,this.addRecord.bind(this))
        }
        let time=this.getDate(date)
        this.props.changeRedux(addRecord({time,moneyItem:money,what,user:userArr[0]}))
    }
    addAlert(title,addRecord){
        alert('', title, [
            { text: '', onPress: addRecord },
            { text: '', onPress: addRecord },
          ])
    }
    addRecord(){
        this.props.changeRedux({type:ADD_RECORD_SUCCESS,addRecordSuccess:null})
    }
    clearBtn(){
        this.setState({ userArr:[],money:'',what:'',date:now})
    }
}
let mapToState=(state)=>{
    return {
        addRecordSuccess:state.interactive.addRecordSuccess,
        userList:state.interactive.userList,
        LoadingShow:state.interactive.LoadingShow
    }
}
let mapToDispatch=(dispatch)=>{
    return {
        changeRedux(obj){
            dispatch(obj)
        }
    }
}
Submit=connect(mapToState,mapToDispatch)(Submit)
export default Submit


